.list_item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  &:hover {
    cursor: pointer;
    .text_box {
      border-top: thick solid black;
    }
  }

  .img_box {
    width: 100%;
    height: 65%;
    display: flex;
    justify-content: center;
    align-items: flex-start;

    .list_img {
      width: 100%;
      height: 100%;

      // background-image: url("https://www.chanel.com/images/q_auto,f_auto,fl_lossy,dpr_auto/w_800/chanel-19-flap-bag-navy-blue-white-red-tweed-gold-tone-silver-tone-ruthenium-finish-metal-tweed-gold-tone-silver-tone-ruthenium-finish-metal-packshot-default-as1160b02877mh694-8826448871454.jpg");
      background-position: center;
      background-size: contain;
      background-repeat: no-repeat;
    }
  }

  .text_box {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    padding-top: 7%;
    padding-bottom: 20%;

    width: 100%;
    height: 30%;
    border-top: thick solid white;
    transition: 0.3s;

    .list_name {
      font-family: abchanel-corpo, 맑은고딕, Malgun Gothic, Apple Gothic, 돋움,
        Dotum, 굴림, Gulim, sans-serif;
      font-weight: 900;
      font-size: 14px;
      letter-spacing: 1px;
    }

    .list_material {
      font-family: 맑은고딕, Malgun Gothic, Apple Gothic, 돋움, Dotum, 굴림,
        Gulim, sans-serif;
      font-weight: 300;
      font-size: 14px;
      padding-top: 1%;
      letter-spacing: 1px;
    }

    .list_price {
      font-family: abchanel-corpo, 맑은고딕, Malgun Gothic, Apple Gothic, 돋움,
        Dotum, 굴림, Gulim, sans-serif;
      font-weight: 900;
      font-size: 14px;
      padding-top: 3%;
      letter-spacing: 1px;

      .gray_star {
        color: gray;
      }
    }

    .list_other {
      font-family: 맑은고딕, Malgun Gothic, Apple Gothic, 돋움, Dotum, 굴림,
        Gulim, sans-serif;
      font-weight: 300;
      font-size: 14px;
      padding-top: 3%;
      letter-spacing: 1px;
    }
  }
}
