html {
    scroll-behavior: smooth;
}

.total {
    position: relative;
    z-index: 50;
    width: 100%;
    height: 360vh;
    background-color: white;
    top: calc(-100vh + 90px);

    .container {

        position: relative;
        background-color: white;


        display: flex;
        text-align: left;

        .dotNav {
            position: fixed;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            height: 80px;
            left: 40px;
            margin-top: 30%;

            .dot1 {
                position: relative;
                margin-bottom: 10px;
                height: 7px;
                width: 7px;
                background-color: gray;
                border-radius: 50%;
                border: 1px solid white;
                transition: 0.2s ease-in;

                &:hover {
                    cursor: pointer;
                }
            }

            .dot2 {
                position: relative;
                margin-bottom: 10px;
                height: 7px;
                width: 7px;
                background-color: gray;
                border-radius: 50%;
                border: 1px solid white;
                transition: 0.2s ease-in;

                &:hover {
                    cursor: pointer;
                }
            }

            .dot3 {
                position: relative;
                margin-bottom: 10px;
                height: 7px;
                width: 7px;
                background-color: gray;
                border-radius: 50%;
                border: 1px solid white;
                transition: 0.2s ease-in;

                &:hover {
                    cursor: pointer;
                }
            }

            .dot4 {
                position: relative;
                margin-bottom: 10px;
                height: 7px;
                width: 7px;
                background-color: gray;
                border-radius: 50%;
                border: 1px solid white;
                transition: 0.2s ease-in;

                &:hover {
                    cursor: pointer;
                }
            }
        }

        .img_slider {
            display: flex;
            flex-direction: column;
            width: 100%;
            height: 300vh;
            padding-left: 10%;
            margin-top: 25%;
            margin-bottom: 20%;

            .img1 {
                width: 100%;
                height: 100%;
                //background-image: url('https://www.chanel.com/images/t_fashionselector/q_auto,f_auto,fl_lossy,dpr_auto/w_1094/chanel-19-flap-bag-navy-blue-white-red-tweed-gold-tone-silver-tone-ruthenium-finish-metal-tweed-gold-tone-silver-tone-ruthenium-finish-metal-packshot-default-as1160b02877mh694-8826448871454.jpg');
