                background-repeat: no-repeat;

                margin-top: 20%;
            }

            .img2 {
                width: 100%;
                height: 100%;
                //background-image: url('https://www.chanel.com/images/t_fashionselector/q_auto,f_auto,fl_lossy,dpr_auto/w_1094/chanel-19-flap-bag-navy-blue-white-red-tweed-gold-tone-silver-tone-ruthenium-finish-metal-tweed-gold-tone-silver-tone-ruthenium-finish-metal-packshot-alternative-as1160b02877mh694-8826448740382.jpg');
                background-size: contain;
                background-repeat: no-repeat;

                margin-top: 10%;
            }

            .img3 {
                width: 100%;
                height: 100%;
                //background-image: url('https://www.chanel.com/images/t_fashionselector/q_auto,f_auto,fl_lossy,dpr_auto/w_1094/chanel-19-flap-bag-navy-blue-white-red-tweed-gold-tone-silver-tone-ruthenium-finish-metal-tweed-gold-tone-silver-tone-ruthenium-finish-metal-packshot-other-as1160b02877mh694-8826449592350.jpg');
                background-size: contain;
                background-repeat: no-repeat;

                margin-top: 10%;
            }

            .img4 {
                width: 100%;
