                //background-image: url('https://www.chanel.com/images/t_fashionselector/q_auto,f_auto,fl_lossy,dpr_auto/w_1094/chanel-19-flap-bag-navy-blue-white-red-tweed-gold-tone-silver-tone-ruthenium-finish-metal-tweed-gold-tone-silver-tone-ruthenium-finish-metal-packshot-extra-as1160b02877mh694-8826448936990.jpg');
                background-size: contain;
                background-repeat: no-repeat;

                margin-top: 10%;
            }
        }

        .product_box {
            background-color: white;
            position: fixed;
            display: flex;
            flex-direction: column;
            height: 700px;
            width: 390px;
            top: 20%;
            left: 60%;

            .name_box {
                height: 8%;
                border-bottom: thick solid black;

                .name {
                    font-family: abchanel-corpo, 맑은고딕, Malgun Gothic, Apple Gothic, 돋움,
                        Dotum, 굴림, Gulim, sans-serif;
                    font-size: 25px;
                    letter-spacing: 1px;
                    font-weight: 600;
                }
            }

            .detail_box {
                margin-top: 5%;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                height: 30%;

                font-family: 맑은고딕, Malgun Gothic, Apple Gothic, 돋움, Dotum, 굴림,
                    Gulim, sans-serif;
                font-weight: 300;
                font-size: 14px;
                letter-spacing: 1px;

                .item_size {
                    margin: 2% 0;
                }

                .unit_box {
                    margin-left: 1%;

                    .unit_change {
                        text-decoration: underline;
                        cursor: pointer;
                    }
                }

                .price {
                    font-family: abchanel-corpo, 맑은고딕, Malgun Gothic, Apple Gothic, 돋움,
                        Dotum, 굴림, Gulim, sans-serif;
                    font-size: 16px;
                    letter-spacing: 2px;
                    font-weight: 700;
                    margin-top: 5%;
                }
            }

            .option_box {
                margin-top: 5%;

                font-family: 맑은고딕, Malgun Gothic, Apple Gothic, 돋움, Dotum, 굴림,
                    Gulim, sans-serif;
                font-weight: 300;
                font-size: 14px;
                width: 120%;

                .option_number {
                    font-size: 12px;
                    font-weight: 500;
                    margin: 5% 0;
                }

                .scroll_btn_on {
                    position: absolute;
                    margin-top: 60px;
                    margin-left: -40px;

                    &:hover {
                        cursor: pointer;
                    }

                    z-index: 1;
                }

                .scroll_btn_off {
                    position: absolute;
                    margin-top: 60px;
                    margin-left: 400px;

                    &:hover {
                        cursor: pointer;
                    }

                    z-index: 1;
                }

                .option_slider {
                    position: relative;
                    height: 100%;
                    width: 82%;

                    .option_category_list {
                        display: flex;
                        height: 20%;
                        border-bottom: thin solid rgba(90, 90, 90, 0.3);

                        .option_category1 {
                            height: 106%;
                            margin-right: 5%;

                            &:hover {
                                cursor: pointer;
                            }

                            .optionUnderline {
                                position: relative;
                                top: -55%;
                                left: 0%;
                                width: 100%;
                                height: 100%;
                                border-bottom: 2px solid black;
                                transition: left 0.1s ease-out;
                            }
                        }

                        .option_category2 {
                            height: 106%;
                            margin-right: 5%;

                            &:hover {
                                cursor: pointer;
                            }
                        }

                        .option_category3 {
                            height: 106%;
                            margin-right: 5%;

                            &:hover {
                                cursor: pointer;
                            }
                        }
                    }

                    .option_img_box {
                        position: relative;
                        width: 100%;
                        height: 70%;
                        overflow: hidden;

                        .option_img_list {
                            position: absolute;
                            left: 0;
                            transition: left 0.5s ease-out;
                            display: flex;
                            justify-content: flex-start;
                            height: 80%;
                            width: 240%;

                            .option_img0 {
                                width: 118px;
                                height: 60px;
                                background-image: url('https://www.chanel.com/images/t_fashionselector/q_auto,f_auto,fl_lossy,dpr_auto/w_262/chanel-19-flap-bag-blue-lambskin-gold-tone-silver-tone-ruthenium-finish-metal-lambskin-gold-tone-silver-tone-ruthenium-finish-metal-packshot-default-as1160b02875n0902-8828578431006.jpg');
                                background-size: contain;
                                background-repeat: no-repeat;
                                margin-top: 3%;
                                margin-right: 2%;

                                &:hover {
                                    cursor: pointer;
                                }
                            }

                            .option_img1 {
                                width: 118px;
                                height: 60px;
                                background-image: url('https://www.chanel.com/images/t_fashionselector/q_auto,f_auto,fl_lossy,dpr_auto/w_262/chanel-19-flap-bag-black-goatskin-gold-tone-silver-tone-ruthenium-finish-metal-goatskin-gold-tone-silver-tone-ruthenium-finish-metal-packshot-default-as1160b0321594305-8829413228574.jpg');
                                background-size: contain;
