                                background-image: url('https://www.chanel.com/images/t_fashionselector/q_auto,f_auto,fl_lossy,dpr_auto/w_262/chanel-19-flap-bag-blue-lambskin-gold-tone-silver-tone-ruthenium-finish-metal-lambskin-gold-tone-silver-tone-ruthenium-finish-metal-packshot-default-as1160b01901n5330-8822635724830.jpg');
                                background-size: contain;
                                background-repeat: no-repeat;
                                margin-top: 3%;
                                margin-right: 2%;

                                &:hover {
                                    cursor: pointer;
                                }
                            }

                            .option_img4 {
                                width: 118px;
                                height: 60px;
                                background-image: url('https://www.chanel.com/images/t_fashionselector/q_auto,f_auto,fl_lossy,dpr_auto/w_262/chanel-19-flap-bag-dark-beige-lambskin-gold-tone-silver-tone-ruthenium-finish-metal-lambskin-gold-tone-silver-tone-ruthenium-finish-metal-packshot-default-as1160b01901n5454-8822634414110.jpg');
                                background-size: contain;
                                background-repeat: no-repeat;
                                margin-top: 3%;
                                margin-right: 2%;

                                &:hover {
                                    cursor: pointer;
                                }
                            }
                        }
                    }
                }

                .find_store_box {
                    display: flex;
                    align-items: center;
                    height: 40%;
                    border-bottom: thin solid rgba(90, 90, 90, 0.3);
                    border-top: thin solid rgba(90, 90, 90, 0.3);
                    width: 82%;

                    .find_store_icon {}

                    .find_store_text {
                        font-size: 12px;
                        font-weight: 500;
                        margin-left: 15%;
                    }
                }

                .law {
                    margin-top: 3%;
                    font-size: 13px;
                    color: rgba(90, 90, 90, 0.8);
                    width: 75%;

                    .law_link {
                        text-decoration: underline;
                    }
                }
            }
