.container {
  display: block;
  font-family: Helvetica, Open-Sans, 'sans-serif-light', sans-serif;
  font-size: 0.775rem;
  font-weight: 300;
  color: #1d1d1d;
  width: 648px;
  height: 1400px;
  position: relative;
  //padding-top: 75px;
  justify-content: center;
  text-align: center;
  //background-color: #fff;
  .accountBox {
    height: 48px;
    .accountText h1 {
      font-size: 40px;
    }
  }
  .customer-nav {
    flex-wrap: wrap;
    justify-content: stretch;
    display: flex;
    width: 100%;
    height: 50px;
    grid-auto-columns: 2fr;
    align-items: center;
    text-align: center;

    .signup {
      justify-content: stretch;
      border-bottom: 3px solid transparent;
      font-size: 14px;
      border-color: #1d1d1d;
      cursor: pointer;
      width: 50%;
      padding-bottom: 10px;
      font-weight: 550;
    }
    .signin-selected {
      justify-content: stretch;
      border-bottom: 0.0625rem solid #d8d8d8;
      cursor: pointer;
      font-size: 14px;
      width: 50%;
      padding-bottom: 10px;
      font-weight: 550;
    }
  }
  .contentBox {
    position: relative;
    width: 100%;
    margin: 0 auto;
    height: 1500px;

    //margin-top: 14.4vw;
  }
  .welcomeText1-r {
    margin-top: 55px;
    font-size: 14px;
  }

  .welcomeText2-r {
    padding-top: 35px;
    font-size: 14px;
    text-align: left;
  }
  .form-control {
    margin-top: 10px;
    width: 350px;
    .form-control small {
      color: red;
      bottom: 0;
    }
    .form-control.error small {
      visibility: visible;
    }
  }
  .formBox input {
    margin-top: 10px;
    background: none;
    border: 0;
    display: block;
    font-size: 13px;
    width: 400px;
    text-rendering: auto;
    color: -internal-light-dark-color(black, white);
    word-spacing: normal;
    text-indent: 0px;
    display: block;
    position: relative;
    border-bottom: 0.0625rem solid #d8d8d8;
    padding-bottom: 0.3125rem;
    padding-top: 45px;
    text-align: left;
    outline: none;
  }
  .form-control input:hover {
    width: 400px;
    border-bottom: 1.5px solid #000;
    transition: all 0.2s ease;
  }
  #register-terms {
    width: 15px;
  }
  .agreeArea {
    display: table-row-group;
    margin-top: 30px;
    .agreeArea input,
    p {
      display: flex;
    }
    .checkBox- #submit {
      background-color: #000000;
      margin-top: 100px;
      width: 100%;
      height: 50px;
      border: 1px solid #000000;
    }
    .buttonBox {
      margin-top: -120px;
      .btnWrapper {
        color: #fff;
        font-size: 11px;
        position: relative;
        font-weight: 600;
        text-transform: uppercase;
        cursor: pointer;
        transition: all 0.5s ease-in-out;
        display: flex;
        align-items: center;
        justify-content: center;
        height: 100%;
        margin-top: -20px;

        #signinbox {
          margin-top: 2%;
          height: 35%;
          border-bottom: 1.8px solid #000;
          transition: all 1s ease;

          #signinbox:hover {
            margin-top: 2%;
            height: 35%;
            cursor: pointer;
            border-bottom: 1.8px solid #fff;
            transition: all 1.1s ease;
          }
        }
      }
    }
  }
}
