.slider2 {
  border: 1px solid red;
  width: 100%;
  height: 100vh;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  display: flex;
  align-items: center;

  .articleGroup {
    margin: 0 auto;
    position: relative;
    border: 1px solid blue;
    width: 45%;
  }

  button.slick-arrow.slick-prev,
  button.slick-arrow.slick-next {
    background-color: #fff;
    border-radius: 50%;
  }

  ::before {
    visibility: visible;
  }

  .logoImg {
    position: fixed;
  }

  .slider2 {
    width: 100%;
    height: 100vh;
    box-sizing: border-box;
    display: flex;
    align-items: center;

    .each-slide2 {
      margin: 0 auto;
      display: flex;
      justify-content: center;
      align-items: center;
      border: 1px solid blue;
      width: 45%;
      //height: 80vh;
      .slideImg2 {
        //padding-left: 202px;
        position: relative;
        justify-content: center;
        width: 440px;
        margin: 0 auto;
        //display: inline;
        .slideImg2 img {
          width: 100%;
          margin: 0 auto;
        }
      }
    }
  }
}
.slide {
  border: 1px solid blue;
  min-width: 100%;
  height: 80%;
  transition: 0.5s;
}
.slick-prev {
  left: -25px;
  background-color: #fff;
  position: absolute;
}

.slick-prev::before {
  visibility: visible;
  position: absolute;
  content: '';
  background-image: url('../../../Images/left1.png');
  background-size: 100% 100%;
  width: 20px;
  height: 35px;
  display: inline-block;
  top: 5px;
  left: -22px;
}
.slick-next::before {
  visibility: visible;
  position: absolute;
  content: '';
  background-image: url('../../../Images/right1.png');
  background-size: 100% 100%;
  width: 20px;
  height: 35px;
  display: inline-block;
  top: 0;
  left: 4;
}
